  height: 100%;
}
.Banner img {
  width: 32%;
}
.ico-sprite {
  background-image: url("assets/sprite.png");
  background-repeat: no-repeat;
  display: inline-block;
  vertical-align: middle;
  width: 40px;
  height: 40px;
}
.ico-back {
  background-position: -54px -1px;
  height: 22px;
  width: 19px;
}
.ico-tick {
