@media screen and (orientation: landscape) and (min-width: 568px) {
body:before {
    content: "Please rotate your device";
    width: 100vw;
    height: 100vh;
    background: #f1f1f1 url("./assets/smartphone.svg") no-repeat 50% 15%;
    background-size: 20%;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    position: fixed;
    z-index: 9999;
    display: block;
    overflow: hidden;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-top: 50vh;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    color: #666;
}
body:after {
    content: "We don't support landscape mode yet. Please go back to potrait mode for best experience";
    width: 90vh;
    height: 25vh;
    text-align: center;
    font-size: 16px;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 5vh;
    z-index: 10000;
    display: block;
    margin: auto;
    color: #666;
}
}
